@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;1,300&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  height: 100vh;
  width: 100vw;
  background-color: #fff;
  overflow-x: hidden;  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100vw;
  height: 100vh;
}




.App-header {
    /* background-color: #282c34; */
    background-color: #111;
    min-height: 12vh;
    width: 100%;
    display: flex;
    color: #fff;
    border-radius: 1px;
  }
  
.App-link {
    color: #fff;
  }

.App-header nav h1 {
  cursor: pointer;
}  



.App-header nav {
    display: flex;
    width: 91%;
    margin: auto;
    align-items: center;
    font-family: 'Amatic SC', cursive;
    font-size: 40px;
    justify-content: space-between;
    font-weight: bolder;
}

.navlinks {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 30%;
}

.navlinks li {
    list-style: none;
}

.navlinks li a{
  position: relative;
  text-decoration: none;
  color: #fff;
  font-size: 35px;
  transition: all 1s ease-out;
}

.navlinks li a:hover {
  bottom: 2px;
  border-bottom: 1px #fff solid;
}

.navextra {
  display: none;
}


.hamburger {
  cursor: pointer;
  width: 60px;
  height: 60px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  -webkit-filter: invert(1);
          filter: invert(1);
  display: none;
  transform: scale(1.2);
}

@media(max-width: 1125px) {
  .navlinks {
    display: none;
  }
  .hamburger {
    display: inline-block;
  }
}

.navextraload {
  position: absolute;
  z-index: 1;
  left: -3vh;
  background-color: #111;
  border-radius: 10px;
  width: 180%;
}

.navextra li {
  padding: 20px;
  width: 90%;
  transition: all 0.5s;
  margin: auto;
}

.navextra li a {
  font-size: 34px;
  border-bottom: 1px #fff solid;
}

.navextra li a:hover {
  bottom: 0;
  border: none;
  letter-spacing: 1px;
}


.container {
    width: 100%;
    height: 80%;
    font-family: 'Poppins', sans-serif;
    
}

.images {
    width: 100%;
    height: 80%;
    background-color: red;
    box-shadow: 0 3px 30px grey;
}

img {
    width: 100%;
    height: 100%;
}

.info {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: auto;
    text-align: center;
    height: 33%;
    align-items: center;
    cursor: default;
}

.overlay {
    position:absolute;
    width: 100%;
    height: 64%;
    top: 12vh;
    transition: all 1s;
    cursor: default;
}

/* .overlay:hover {
    background-color: rgba(0, 0, 0, 0.3);
} */

.overlay h1 {
    position: relative;
    width: 80%;
    top: 10vh;
    font-size: 80px;
    color: #fff;
    margin: auto;
    text-shadow: 0 3px 5px black;
}

.overlay p {
    position: relative;
    width: 80%;
    top: 10vh;
    font-size: 15px;
    max-width: 50%;
    color: #fff;
    margin-left: 10.5%;
    text-shadow: 0 3px 5px black;
}



@media (max-width: 767px) {
    .overlay h1 {
        padding-top: 20px;
        font-size: 50px;
    }
    .overlay p {
        font-size: 12px;
        padding-top: 10px;
        max-width: 80%;
    }
}

@media (max-width: 1078px) {
    .overlay p{
        max-width: 80%;
    }
    .overlay {
        height: 65%;
    }
}

.content {
    margin: auto;
    /* width:90%; */
    border: none;
    margin-bottom: 40px;
    margin-top: 80px;
}

.article {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /* margin-bottom: 40px; */
    box-shadow: 0 3px 10px gray;
    background-color: #fff;
    font-family: 'Poppins', sans-serif;
    transition: all 1s;
    background-color:rgba(0,0,0,0.2);
    /* border-radius: 10px; */
}


.content :nth-child(2), .content :nth-child(4), .content :nth-child(6) {
    direction: rtl;
}

.article :nth-child(2) {
    direction: ltr;
}

img {
    align-self: center;
    width: 100%;
    height: 100%;
    cursor: pointer;
    /* border-top-left-radius: 5px;
    border-bottom-left-radius: 5px; */
    transition: all 1s;
}

.text {
    display: grid;
    justify-self: center;
    align-self: center;
    font-size: 15px;
    background-color: #fff;
    width: 100%;
    height: 100%;
    margin: auto;
    transition: all 1s;
    color: #111;
    /* border-top-right-radius: 5px;
    border-bottom-right-radius: 5px; */
}

.text h1 {
    font-size: 30px;
    padding-bottom: 10px;
    padding-top: 10px;
    cursor: default;
}

.text p {
    padding-bottom: 10px;
    padding-top: 10px;
    line-height: 2;
    cursor: default;
}

.innertext {
    margin: auto;
    max-width: 60%;
}

.innertext li {
    cursor: pointer;
    list-style: none;
    background-color: #111;
    color: #fff;
    border-radius: 5px;
    margin-top: 10px;  
    margin-bottom: 40px; 
    padding: 10px;
    width: 120px;
    transition: all 1s;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}

.innertext li a {
    transition: all 0.5s;
    text-decoration: none;
    color: #fff;
}

.innertext li a:hover {
    letter-spacing: 0.5px;
}

@media (max-width: 1050px) {
    .article {
        grid-template-columns: repeat(1, 1fr);
        margin-bottom: 40px;
    }

    img {
        width: 100%;
        height: 100%;
        border-radius: 0;
        /* border-top-right-radius: 5px;
        border-top-left-radius: 5px; */
    }

    .innertext p{
        padding-bottom: 20px;
    }
    .innertext h1{
        padding-top: 30px;
    }
    .text {
        border-radius: 0;
        /* border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px; */
    }
    .innertext li {
        margin-top: 0px;
    }
}

.text:hover {
    -webkit-filter: invert(1);
            filter: invert(1);
}

.footer {
    font-family: 'Poppins', sans-serif;
    background-color: rgb(19, 19, 19);
    width: 100%;
    color:#fff;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.footer h1 {
    font-size: 70px;
    padding-top: 50px;
    padding-bottom: 50px;
}

@media(max-width: 1050px ) {
    .footer {
        flex-direction: column;
        text-align: center;
    }

    .footer p {
        padding-bottom: 50px;
        width: 90%;
    }
}

@media(max-width: 408px) {
    .footer h1 {
        font-size: 50px;
    }
}
